$darkBlue: #061925;
$darkGreen: #2e5266;
$lightGreen: #129494;
$beige: #fcf8e6;

@mixin flex-column-center {
    display: flex;
    flex-direction: column;
    align-items: center;
}
  
.error{
    &__div{
        @include flex-column-center();
        text-align: center;
        background-color: rgba(255, 255, 255, 0.651); 
        border: 5px solid $darkBlue; 
        border-radius: 10px;
        font-size: 3rem;
        color:#061925;
        text-shadow: 2px 2px 2px white;
        font-weight: 400;
        padding: 48px 16px;
        @media all and (max-width: 920px){
            font-size: 1.9rem;
        }
        @media all and (max-width: 600px){
            font-size: 1.5rem;
            border-radius: 0;
        }
        @media all and (max-width: 376px){
            font-size: 1rem
        }
    }
    &__link{
        text-decoration: none;
        color: $lightGreen;
        
    }
    h2{
        font-size: 20rem;
        margin: 0;
        @media all and (max-width: 600px){
            font-size: 10rem;
        }
        @media all and (max-width: 376px){
            font-size: 7rem;
        }
    }

}