
.curriculum{
  background-color: white;
  border: 8px solid black;
  margin: 0;
  padding: 32px 8px;
  max-width: 90%;
  margin: 0 auto ;
}
// TIMELINE
.vertical-timeline {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
  margin: 0 auto;;
  padding: 0;
  width: 100%;
}

.vertical-timeline-element {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  margin-bottom: 0px;
  position: relative;
  width: 95%;
}

.vertical-timeline-element:before {
  // Permet d'afficher la ligne noire
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  height: 100%;
  width: 6px;
  background: #061925;
  z-index: 10;
}
  
.vertical-timeline-element:nth-child(even) {
  // Cases paires
  flex-direction: row-reverse;
  justify-content: right;
  margin: 0;
  padding: 0;
}
  
.vertical-timeline-element-content {
  align-items: center;
  padding-right: 0;
  padding-bottom: 150px;
  margin-right: -5%;
  width: 50%;
  @media all and (min-width: 1900px){
    margin-right: -3%}
  @media all and (max-width: 1900px){
    margin-right: -4%}
  @media all and (max-width: 1450px){
    margin-right:-5%;
  }
  @media all and (max-width: 1100px){
    margin-right:-6%;
  }
  @media all and (max-width: 950px){
    margin-right: -7%;
    padding-bottom: 60px;
  }
  @media all and (max-width: 820px){
    margin-right: -8%;
  }
  @media all and (max-width: 700px){
    margin-right: -10%;
  }
  @media all and (max-width: 600px){
    margin-right: -2%;
    padding-bottom: 0px;
    width: 45%
  }
  @media all and (max-width: 500px){
    margin-right: -3%;
  }
  @media all and (max-width: 420px){
    margin-right: -5%;
  }
  h3, h4, p{
    margin: 4px 0;
    padding: 0 40px;
    @media all and (max-width: 950px){
      padding: 0 45px;
    }
  }
  h3{
    text-align: left;
    font-size: 1.6rem;
    color: #0b6363;
    @media all and (max-width: 600px){
      font-size: 1.3rem;
      padding: 0 35px;
    }
    @media all and (max-width: 420px){
      font-size: 0.7rem;
      padding: 0 25px
    }
  }
  h4{
    font-size: 1.3rem;
    color:#061925;
    @media all and (max-width: 600px){
      font-size: 0.7rem;
      padding: 0 35px;
    }
    @media all and (max-width: 420px){
      padding: 0 25px
    }
  }
  p{
    @media all and (max-width: 600px){
      font-size: 0.7rem;
      padding: 0 35px
    }
    @media all and (max-width: 420px){
      padding: 0 25px
    }
  }
}


.vertical-timeline-element-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 50;
  background: white;
  border: 5px solid black;
  color: #023352d1;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  @media all and (max-width: 600px){
    width: 60px;
    height: 60px;
  }
  @media all and (max-width: 376px){
    width: 50px;
    height: 50px;
  }
  img{
    width: 35px;
    height:35px;
  }
}

.parcours__sup{
  .section__header{
    margin-top:0;
  }
}